        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额" align="right" width="150">
          <template slot-scope="scope">
            <el-input size="small" type="number" class="editor" v-model="scope.row.common_price" @change="handleEdit(scope.row,'common_price')" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="300">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" maxlength="300" class="editor" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="图片附件" :show-overflow-tooltip="true" :render-header="renderHeader1" min-width="400">
          <template slot-scope="scope">
            <div style="width:44px;">
              <el-upload class="custom-avatar-uploader" :headers='headers' :data="extendParams" :action="imgUrl" list-type="picture-card" accept="image/*" :limit="imgLimit" :file-list="scope.row.fileList" :on-preview="handlePictureCardPreview" :on-remove="(file,fileList)=>{handleRemove(file,fileList,scope.row)}" :on-success="(response, file, fileList)=>{handleAvatarSuccess(response,file, fileList,scope.row)}" :on-exceed="handleExceed" :on-error="imgUploadError">
                <div @click="uploadImg">
                  <i class="el-icon-plus"></i>
                </div>
              </el-upload>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <span class="attention">*输入金额后请按Enter键进行确认 </span> -->
    <div class="aboutList" v-show="is_disabled && hexiaoDetail">
      <div class="paymanageTitle fcsb">
        <span>核销明细</span>
        <el-link type="primary" @click="exportDataEvent">导出核销明细</el-link>
      </div>
      <div class="table_container" style="margin-top:20px">
        <div>
          <vxe-table border @select-change="SelectionChange" show-overflow show-header-overflow size="mini" :keyboard-config="{isArrow: true, isDel: false, isTab: true, isEdit: true}" show-footer :footer-method="footerMethod" @edit-closed="editClosedEvent" align="center" highlight-cell max-height="500" highlight-hover-row highlight-current-row resizable ref="xTable" :mouse-config="{selected: true}" :edit-config="{trigger: 'click', mode: 'cell'}" :checkbox-config="{checkStrictly: true}">
            <vxe-table-column type="checkbox" width="50"></vxe-table-column>
            <vxe-table-column type="seq" width="60" title="序号"></vxe-table-column>
            <vxe-table-column field="bill_sn" width="120" title="单据号"></vxe-table-column>
            <vxe-table-column field="order_type" width="120" title="单据类型"></vxe-table-column>
