            <vxe-table-column field="bill_num" width="120" title="出库数量"></vxe-table-column>
            <vxe-table-column field="order_price" width="120" title="出库金额"></vxe-table-column>
            <vxe-table-column field="ec_channel_fee" width="120" title="通道费" :visible="Boolean(!is_company_service)"></vxe-table-column>
            <vxe-table-column field="sc_price" title="实际应收金额" width="130">
              <template v-slot:header>
                <vxe-tooltip v-model="showSexTip" content="实际应收金额=本出库单中出库金额">
