            <vxe-table-column field="remark" width="120" title="备注"></vxe-table-column>
            <vxe-table-column field="rc_price" width="120" title="已冲销金额"></vxe-table-column>
            <vxe-table-column field="bill_receipt_discount_price" width="120" title="已优惠金额" :visible="Boolean(is_company_service)"></vxe-table-column>
            <vxe-table-column field="rest_price" width="120" title="未冲销金额"></vxe-table-column>
            <vxe-table-column field="bill_discount_price" width="120" title="本次优惠金额" :edit-render="{name: 'vNumber'}" :visible="Boolean(is_company_service)"></vxe-table-column>
            <vxe-table-column field="bill_price" width="120" title="本次冲销金额" :edit-render="{name: 'vNumber'}"></vxe-table-column>
          </vxe-table>
        </div>
      </div>
    </div>
    <el-dialog :close-on-click-modal="false" title="提示" :visible.sync="dialogVisible" width="20%">
      <span>{{msg}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible=false,goto=0">取 消</el-button>
        <el-button type="primary" @click="confirm()">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 客户收款，往来类型：销售收款，结算金额合计大于核销明细合计时 -->
    <el-dialog :close-on-click-modal="false" title="提示" :visible.sync="dialogVisiblehj" width="20%">
      <span>本次冲销多结算{{(Number(jshj)-Number(cxhj)).toFixed(2)}}元，将自动转为预收款，是否继续？</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('params','Y')">是</el-button>
        <el-button @click="dialogVisiblehj=false">否</el-button>
      </span>
    </el-dialog>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submitForm('params')">确 定</el-button>
    </div>
    <el-dialog :visible.sync="dialogVisibleimg" width="30%">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
