<script>
import { opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import { serverMoudle } from "@/utils/fetch";
export default {
  name: "customeReceiptmanageAdd",
  data() {
    return {
      params: {
        company_id: null,
      },
      headers: {
        token: localStorage.getItem("token"),
      },
      extendParams: {},
      associated_customer_id: "",
      dialogImageUrl: "",
      jshj: 0, //结算金额合计
      cxhj: 0, //核销明细合计
      bcyh: 0, //本次优惠合计
      imgLimit: 6,
      isMultiple: true,
      showSexTip: false,
      // 这是下边
      authority: {},
      title: "添加",
      loading1: false,
      msg: null,
      goto: 0,
      bankList: [],
      imgIndex: {},
      dialogVisible: false,
      dialogVisiblehj: false,
      sumBill_price: null,
      theForm: {},
      showDelete: false,
      dialogVisibleimg: false,
      newPic: "",
      imgUrl: `${process.env.VUE_APP_BASE_API}/v2/open/CustomerReceipt/receiptUpload`,
      // params: {},
      TableData: [],
      companyList: [],
      multipleSelection: [],
      per_page: [30, 50, 100, 500],
      index: 0,
      customer_id: null, //客户名称
      receipt_id: null,
      customer_name: "",
      isDisabled: false,
      is_disabled: false,
      PD: [],
      relationTable: [],
      changeIndex: 0,
      xCheck: "N",
      hexiaoDetail: true,
      repetition: true, // wz alter 判断是否可以执行 editlist 这个方法
    };
  },
  computed: {
    ...mapGetters(["receiptmanageAdd_showGet"]),
  },
  methods: {
    uploadImg(event) {
      if (!this.params.customer_id) {
        this.$message.warning("请先选择客户名称");
        event.preventDefault();
        event.stopPropagation();
      }
    },
    ...mapActions(["receiptmanageIndexACT"]),
    // 图片预览
    handlePictureCardPreview(file) {
      //预览图片时调用
      this.dialogImageUrl = file.url;
      this.dialogVisibleimg = true;
    },
    editClosedEvent({ row }, event) {
      if (row.bill_price > 0) {
        if (Number(row.rest_price) < 0) {
          row.bill_price = 0;
        }
      } else {
        if (Number(row.rest_price) > 0) {
          row.bill_price = 0;
        }
      }
      this.$refs.xTable.setSelection(row, true);
      this.$refs.xTable.updateFooter();
    },
    exportDataEvent() {
      this.$refs.xTable.exportData({
        filename: "核销明细",
        type: "csv",
      });
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (
            [
              "sc_price",
              "rc_price",
              "rest_price",
              "bill_price",
              "bill_discount_price",
            ].includes(column.property)
          ) {
            this.cxhj = this.$utils.sum(data, "bill_price").toFixed(2);
            this.bcyh = this.$utils.sum(data, "bill_discount_price").toFixed(2);
            return this.$utils.sum(data, column.property).toFixed(2);
          }
          return null;
        }),
      ];
    },
    handleRemove(file, fileList, row) {
      let newarry = fileList.map((itm) => {
        if (!!itm.response) {
          return itm.response.data[0];
        } else {
          return itm.name;
        }
      });
      this.$set(row, "imglist", newarry);
    },
    handleAvatarSuccess(response, file, fileList, row) {
      let newarry = fileList.map((itm) => {
        if (!!itm.response) {
          return itm.response.data;
        } else {
          return itm.name;
        }
      });
      this.$set(row, "imglist", newarry);
      //图片上传成功
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    handleExceed(files, fileList) {
      //图片上传超过数量限制
      this.$message.error("上传图片不能超过6张!");
    },
    imgUploadError(err, file, fileList) {
      //图片上传失败调用
      this.$message.error("上传图片失败!");
    },
    // 上边是上传图片
    renderHeader() {
      let str = "";
      switch (this.params.accounts_type_id) {
        case 10001: //销售收款
        case 10002: //物流代收
        case 3: //电商结算
          str = "实际应收金额=本出库单中出库金额";
          break;
        case 10003: //预收款
        case 10004: //其他收款
          this.hexiaoDetail = false;
          break;
        default:
          break;
      }
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">{str}</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold"
          >
            实际应收金额
          </span>
        </el-tooltip>
      );
    },
    renderHeader1() {
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content" style="width:250px;">
            建议上传付款凭证，如汇款单等；图片支持jpg、jpeg、png、bmp格式，大小不超过2MB，最多上传6张；
          </p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            图片附件
          </span>
        </el-tooltip>
      );
    },
    goBack() {
      this.$publicFution.goPage(this.$route.query.receipt_id)
      this.$router.push({
        path: "/custom/receiptmanage",
      });
    },
    getBanklist(row) {
      this.$set(row, "bank_number", row.bank_id.bank_number);
    },
    confirm() {
      this.goto = 1;
      this.dialogVisible = false;
      let parnam = Object.assign({}, this.params, {
        common_body: JSON.stringify(this.TableData),
        is_verify: 1,
        goto: this.goto,
      });
      if (!!this.$route.query.receipt_id) {
        this.editlist(parnam);
      } else {
        this.githexmx(parnam);
      }
    },
    handleEdit(val, key) {
      if (val.rest_price > 0) {
        let result =
          Number(val.bill_price) >= 0 &&
          Number(val.bill_price) <= Number(val.rest_price)
            ? Number(val.bill_price)
            : 0;
        this.$nextTick(() => {
          val.bill_price = result;
        });
      } else if (val.rest_price < 0) {
        //未冲销金额小于0
        let result =
          Number(val.bill_price) >= Number(val.rest_price) &&
          Number(val.bill_price) <= 0
            ? Number(val.bill_price)
            : 0;
        this.$nextTick(() => {
          val.bill_price = result;
        });
      }
      if (!val) {
        this.$nextTick(() => {
          val[key] = val[key].toFixed(2);
        });
      }
    },
    SelectionChange({ selection, row, checked }) {
      let numberSun = [];
      if (selection.length) {
        if (this.params.accounts_type_id == 10002 && !this.params.company_id) {
          this.companyList = [];
          this.companyList.push({
            company_id: selection[0].finance_company_id,
            company_name: selection[0].finance_company_name,
          });
          this.$set(this.params, "company_id", selection[0].finance_company_id);
          this.companyChange(selection[0].finance_company_id);
          this.$refs["params"].validate((valid) => {});
        }
        selection.forEach((item) => {
          if (item.bill_price == undefined) {
            this.$set(item, "bill_price", "0.00");
          }
          numberSun.push(Number(item.bill_price));
        });
        this.sumBill_price = numberSun.reduce((num, item) => {
          return num + item;
        });
        var bill_priceSum = this.jshj - this.sumBill_price;
      }
      if (checked) {
        if (this.jshj >= 0) {
          //为正数或者为0
          if (Number(row.rest_price) > 0) {
            if (bill_priceSum >= Number(row.rest_price)) {
              //如果A>=B
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (
              //如果A>0并且A<B
              bill_priceSum >= 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              //如果A<0并且A<B
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = 0;
            } else {
              //都不满足直接等于A
              row.bill_price = Number(bill_priceSum).toFixed(2);
            }
          } else {
            row.bill_price = Number(row.rest_price).toFixed(2);
          }
        } else {
          //结算合计为负数
          if (Number(row.rest_price) > 0) {
            //勾选的正数行
            if (bill_priceSum > 0 && bill_priceSum >= Number(row.rest_price)) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (
              bill_priceSum >= 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = 0;
            }
          } else {
            if (bill_priceSum >= Number(row.rest_price) && bill_priceSum < 0) {
              row.bill_price = Number(bill_priceSum).toFixed(2);
            } else if (
              bill_priceSum < 0 &&
              bill_priceSum < Number(row.rest_price)
            ) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            } else if (bill_priceSum >= 0) {
              row.bill_price = Number(row.rest_price).toFixed(2);
            }
          }
        }
      } else {
        row.bill_price = 0;
      }
      this.$refs.xTable.updateFooter();

      let sumeveny = this.$refs.xTable.footerData[0];
      this.cxhj = sumeveny[sumeveny.length - 1];
    },
    editlist(val) {
      //修改客户收款单
      let parns = Object.assign(val, {
        receipt_id: this.$route.query.receipt_id,
      });
      this.$axMethod.customEditReceipt(parns).then(({ data }) => {
        if (data.code == 200) {
          this.$message({
            message: data.msg,
            type: "success",
          });
          this.goBack();
        } else if (data.code == 409) {
          this.msg = data.msg;
          this.dialogVisible = true;
        }
      });
    },
    githexmx(val) {
      let { company_id, customer_id, accounts_type_id } = val;
      if (![10002, 3].includes(accounts_type_id)) {
        //如果选择的不是物流代收和电商结算，那么就必须选择客户
        if (!customer_id) {
          return false;
        }
      }
      this.$axMethod.getReceiptDetailed(val).then(({ data }) => {
        if (data.code == 200) {
          if (data.data instanceof Array) {
            this.is_disabled = true;
            if (this.$refs.xTable) {
              this.$refs.xTable.reloadData(data.data);
              this.$refs.xTable.updateFooter();
            }
          }
        }
      });
    },
    datalist(val) {
      //请求列表
      let { company_id, customer_id, accounts_type_id } = val;
      if (![10002, 3].includes(accounts_type_id)) {
        //如果选择的不是物流代收和电商结算，那么就必须选择客户
        if (!customer_id) {
          return false;
        }
      }
      this.$axMethod.customAddReceipt(val).then(({ data }) => {
        if (data.code == 200) {
          if (data.data instanceof Array) {
            this.is_disabled = true;
            if (this.$refs.xTable) {
              this.$refs.xTable.reloadData(data.data);
              this.$refs.xTable.updateFooter();
            }
            if (data.data.length === 0) {
              // 新增不返回列表
              this.$message({
                message: data.msg,
                type: "success",
              });
              this.goBack();
            }
          } else {
            this.$message({
              message: data.msg,
              type: "success",
            });
            this.goBack();
          }
        } else if (data.code == 409) {
          this.msg = data.msg;
          this.dialogVisible = true;
        }
      });
    },
    receivables(val, index) {
      //结算方式
      if (val.receivables_mode_id == 10007) {
        this.$axMethod
          .getCustomerInfoOpen({
            customer_id: this.params.customer_id,
            company_id: this.params.company_id,
            associated_customer_id: this.associated_customer_id,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              this.$set(
                val,
                "common_price",
                data.data.available_rebate_balance
              );
              this.$set(val, "bank_number", "");
              this.$set(val, "bank_id", {
                bank_id: "",
              });
            }
          });
        this.$nextTick(() => {
          this.$refs.table.bodyWrapper.scrollTop =
            this.$refs.table.bodyWrapper.scrollHeight + 33;
        });
      }
    },
    submitForm(formName, DifferenceValue = "N") {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let vm = this;
          var sumbitData = this.TableData.filter((val) => {
            for (var i in val) {
              if (val[i] !== undefined) {
                return val[i];
              }
            }
          });
          if (sumbitData.length < 1) {
            this.$message.error("结算明细不能为空");
            return false;
          } else {
            var box = ["common_price", "receivables_mode_id"];
            let result = sumbitData.every((item) => {
              var step = true;
              box.forEach((val) => {
                if (
                  item[val] === undefined ||
                  item[val] === null ||
                  item[val] === ""
                ) {
                  step = false;
                }
              });
              return step;
            });
            if (!result && this.is_company_service != 1) {
              this.$message.warning("请完善 结算明细 表体信息");
              return false;
            }
          }
          if ([10002, 10001, 3].includes(this.params.accounts_type_id)) {
            if (!this.$refs.xTable.getData().length) {
              this.$message.error("核销明细不能为空");
              return false;
            } else {
              if (!this.$refs.xTable.getSelectRecords().length) {
                this.$message.error("请选择至少一条核销明细");
                return false;
              }
            }
          }
          //1251客户收款 auth:wxy 产品：zyl
          if (this.params.accounts_type_id == 10001 && DifferenceValue == "N") {
            //往来类型是销售收款 判断结算金额合计大于核销明细合计时 弹框
            if (this.is_company_service) {
              // params.discount_price   优惠金额
              // bcyh  本次优惠
              if (
                Number(this.bcyh) !=
                Number(
                  this.params.discount_price ? this.params.discount_price : 0
                )
              ) {
                this.$message({
                  message:
                    '创建收款单据失败，请检查优惠金额是否在"本次优惠金额"列下全部核销',
                  type: "warning",
                });
                return false;
              }
              if (Number(this.jshj) != Number(this.cxhj)) {
                this.$message({
                  message:
                    '创建收款单据失败，请检查填写的金额是否在"本次核销金额"列下全部核销',
                  type: "warning",
                });
                return false;
              }
            } else {
              if (Number(this.jshj) > Number(this.cxhj)) {
                this.dialogVisiblehj = true;
                return false;
              }
            }
          }
          let s = sumbitData.map((item) => {
            return {
              common_price: item.common_price,
              imglist: (function () {
                if (typeof item.imglist != "undefined") {
                  return item.imglist;
                } else {
                  let fileName = [];
                  item.fileList &&
                    item.fileList.forEach((val) => {
                      if (val.url != "") {
                        fileName.push(val.name);
                      }
                    });
                  return fileName;
                }
              })().join(),
              receivables_mode_id: item.receivables_mode_id,
              bank_id:
                item.bank_id == undefined || item.bank_id.bank_id == 0
                  ? ""
                  : item.bank_id.bank_id,
              remark: item.remark,
            };
          });
          let _bill_body = this.$refs.xTable.getSelectRecords();
          _bill_body.forEach((item) => {
            item._XID && delete item._XID;
            item.checked = 1; // 后台那边需要的字段 选中项
          });
          let parnam = Object.assign({}, this.params, {
            logistics_number: this.params.logistics_number
              ? this.params.logistics_number
              : "",
            common_body: JSON.stringify(s),
            is_verify: 0,
            goto: this.goto,
            bill_body: JSON.stringify(_bill_body),
          });
          if (!!this.$route.query.receipt_id) {
            this.editlist(parnam);
          } else {
            this.datalist(parnam);
          }
        }
      });
    },
    changeLogistics(val) {
      //物流公司
      this.remove();
      let parnam = Object.assign({}, this.params, {
        common_body: JSON.stringify(this.TableData),
        is_verify: 1,
        goto: this.goto,
      });
      this.githexmx(parnam);
    },
    customerDetails(val) {
      this.$axMethod
        .getCustomerInfoNEWqx({
          customer_id: val,
          company_id: this.params.company_id,
        })
        .then(({ data }) => {
          if (data.code == 200) {
            console.log(data.data);

            [
              "deposits",
              "balance",
              "credit_balance",
              "credit_quota",
              "contact_name",
              "customer_mobile",
              "business_id",
            ].forEach((item) => {
              this.$set(this.params, item, data.data[item]);
            });
            Object.assign(data.data, {
              employee_id: data.data.business_id,
              employee_name: data.data.business_name,
            });
            opthionList(this, data.data, "customer_id");
          }
        });
    },
    changeCustomer(val, index) {
      this.extendParams = {
        customer_id: this.params.customer_id,
        sign: serverMoudle({
          customer_id: this.params.customer_id,
        }),
      };
      let clearArr = [10003, 10004]; // 预收款 其他收款
      //头部切换更改
      if (this.TableData.length > 0) {
        this.$set(this, "TableData", []);
      }
      this.jshj = 0; // 初始化
      // if (clearArr.includes(this.params.accounts_type_id)) {
      //   return false;
      // }
      if (index != 2) {
        let parnam = Object.assign({}, this.params, {
          common_body: JSON.stringify(this.TableData),
          is_verify: 1,
          goto: this.goto,
        });
        if (!clearArr.includes(this.params.accounts_type_id)) {
          this.githexmx(parnam);
        }
      } else {
        //选择的是客户
        this.$set(this.params, "business_id", ""); //业务员
        this.associated_customer_id = this.customerList.filter(
          (obj) => obj.customer_id === val
        )[0].associated_customer_id;
        this.customerDetails(val);
        if (val) {
          this.$refs["params"].validate((valid) => {
            if (valid) {
              //有业务员
              if ([10003, 10004].includes(this.params.accounts_type_id)) {
                this.is_disabled = false;
                if (this.$refs.xTable) {
                  this.$refs.xTable.reloadData([]);
                }
              } else {
                let parnam = Object.assign({}, this.params, {
                  common_body: JSON.stringify(this.TableData),
                  is_verify: 1,
                  goto: this.goto,
                });
                if (!clearArr.includes(this.params.accounts_type_id)) {
                  this.githexmx(parnam);
                }
              }
              // });
            }
          });
        } else {
          let parnam = Object.assign({}, this.params, {
            common_body: JSON.stringify(this.TableData),
            is_verify: 1,
            goto: this.goto,
          });
          if (!clearArr.includes(this.params.accounts_type_id)) {
            this.githexmx(parnam);
          }
        }
      }
    },
    addF() {
      if (!!this.params.accounts_type_id) {
        this.TableData.push({});
      } else {
        this.$message.warning("请选择往来类型");
      }
    },
    remove() {
      this.$set(this, "TableData", []);
    },
    delF() {
      if (this.multipleSelection.length < 1) {
        this.$message.error("请选择要删除的数据");
        return false;
      }
      this.multipleSelection.forEach((val) => {
        let index = this.TableData.indexOf(val);
        return this.TableData.splice(index, 1);
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    accountsTypeChange(val) {
      //往来类型
      // 菲亚，雅丽让去掉  往来类型关联关系
      if (val) {
        this.remove();
        // let arr = ["department_id", "business_id", "logistics_id"];
        let arr = ["department_id", "logistics_id"];
        arr.forEach((item) => {
          this.$set(this.params, item, null);
        });
        if ([10002, 3].includes(val)) {
          this.$nextTick(() => {
            this.$refs["customer_id"].clearValidate();
          });
        }
        if ([10003, 10004].includes(val)) {
          this.is_disabled = false;
          if (this.$refs.xTable) {
            this.$refs.xTable.reloadData([]);
            this.$refs.xTable.updateFooter();
          }
          return false;
        }
        this.$nextTick(() => {
          this.$refs["params"].validate((valid) => {
            let parnam = Object.assign({}, this.params, {
              common_body: JSON.stringify(this.TableData),
              is_verify: 1,
              goto: this.goto,
            });
            this.githexmx(parnam);
          });
        });
      }
    },
    companyChange(val) {
      let exclude = [];
      if (this.params.accounts_type_id == 10002) {
        exclude = [
          "company_id",
          "business_time",
          "accounts_type_id",
          "logistics_number",
        ];
      } else {
        exclude = ["company_id", "business_time", "accounts_type_id"];
      }
      for (var i in this.params) {
        if (!exclude.includes(i)) {
          this.params[i] = null;
        }
      }
      this.$set(this, "TableData", []); //清空核算明细
      if (this.$refs.xTable) {
        this.$refs.xTable.reloadData([]);
        this.$refs.xTable.updateFooter();
      }
      this.$nextTick(() => {
        let parnam = Object.assign({}, this.params, {
          common_body: JSON.stringify(this.TableData),
          is_verify: 1,
          goto: this.goto,
        });
        this.githexmx(parnam);
      });
    },
    departmentChange(val) {
      this.$set(this.params, "business_id", null);
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map((item) => Number(item[column.property]));
        if (!values.every((value) => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 4) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            this.jshj = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
  },
  mounted() {
    let receiptId = this.$route.query.receipt_id;
    this.title = !receiptId ? "添加" : "修改";
    if (!!receiptId) {
      let obj = {
        receipt_id: receiptId,
        is_edit: 1,
      };
      this.isDisabled = true;
      this.$axMethod.customGetReceiptInfo(obj).then(({ data }) => {
        if (data.code == 200) {
          let datataple = data.data;
          opthionList(this, datataple); //渲染params
          this.employeeList.push({
            employee_id: datataple.business_id,
            employee_name: datataple.business_name,
          });
          this.customerList = [
            {
              customer_name: datataple.customer_name,
              customer_id: datataple.customer_id,
              address: datataple.customer_id,
              customer_mobile: datataple.customer_mobile,
              contact_name: datataple.contact_name,
            },
          ];
          if (datataple.receipt_common_bill.length) {
            this.$set(this, "is_disabled", true);
          }
          this.$axMethod
            .getCustomerInfoNEWqx({
              customer_id: datataple.customer_id,
              company_id: datataple.company_id,
            })
            .then(({ data }) => {
              if (data.code == 200) {
                [
                  "deposits",
                  "balance",
                  "credit_balance",
                  "credit_quota",
                  "contact_name",
                  "customer_mobile",
                  "business_id",
                ].forEach((item) => {
                  this.$set(this.params, item, data.data[item]);
                });
              }
            });
          this.$set(this, "params", {
            shopping_order_sn: datataple.shopping_order_sn,
            accounts_type_id: datataple.accounts_type_id,
            business_time: datataple.business_time,
            company_id: datataple.company_id,
            discount_price: datataple.discount_price,
            logistics_number: datataple.logistics_number,
            department_id: datataple.department_id,
            logistics_id: datataple.logistics_id,
            customer_id: datataple.customer_id,
            receipt_sn: datataple.receipt_sn,
            ec_order_sn: datataple.ec_order_sn,
            remark: datataple.remark,
          });
          // 对象循环渲染 改变结构
          this.$set(this, "TableData", datataple.receipt_common);
          datataple.receipt_common.forEach((item, index) => {
            this.bankList.push({
              bank_id: item.bank_id,
              bank_number: item.bank_number,
              bank_name: item.bank_name,
            });
            this.receivablesList.push({
              receivables_mode_name: item.receivables_mode_name,
              receivables_mode_id: item.receivables_mode_id,
            });
            this.$set(this.TableData[index], "bank_id", {
              bank_id: item.bank_id,
              bank_number: item.bank_number,
              bank_name: item.bank_name,
            });
          });
          if (this.$refs.xTable) {
            this.$refs.xTable.reloadData(datataple.receipt_common_bill);
            this.$refs.xTable.updateFooter();
          }
          let filetes = datataple.receipt_common_bill.filter((val) => {
            return val.checked == 1;
          });
          if (filetes.length) {
            this.$nextTick(() => {
              filetes.forEach((row) => {
                this.$refs.xTable.toggleCheckboxRow(row);
                let sumeveny = this.$refs.xTable.footerData[0];
                this.cxhj = sumeveny[sumeveny.length - 1];
              });
            });
          }
          this.extendParams = {
            customer_id: this.params.customer_id,
            sign: serverMoudle({
              customer_id: this.params.customer_id,
            }),
          };
        }
      });
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date()),
      });
      opthionList(this, localStorage);
    }
    this.getCollectingBankInvok();
  },
};
</script>
